/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after {
  content: '';
  content: none;
}

q:before, q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

html {
  font-size: 16px;
  font-family: 'Roboto', sans-serif;
}

body {
  min-height: 100vh;
}

body.touch #pika {
  display: none;
}

body i {
  font-style: italic;
}

#pika {
  width: 50px;
  position: absolute;
  top: 200px;
  left: 200px;
  transition: all 3s ease-out 0s;
  z-index: 1;
}

#pika .cls-1 {
  fill: #f62d14;
}

#pika .cls-1,
#pika .cls-10,
#pika .cls-12,
#pika .cls-16,
#pika .cls-17,
#pika .cls-18,
#pika .cls-19,
#pika .cls-20,
#pika .cls-8 {
  stroke: #000;
}

#pika .cls-1,
#pika .cls-10,
#pika .cls-12,
#pika .cls-16,
#pika .cls-17,
#pika .cls-18,
#pika .cls-19,
#pika .cls-2,
#pika .cls-20,
#pika .cls-21,
#pika .cls-22,
#pika .cls-23,
#pika .cls-3,
#pika .cls-4,
#pika .cls-6,
#pika .cls-7,
#pika .cls-8 {
  stroke-miterlimit: 10;
}

#pika .cls-2 {
  fill: #fad61d;
}

#pika .cls-2,
#pika .cls-21,
#pika .cls-22,
#pika .cls-23,
#pika .cls-3,
#pika .cls-4,
#pika .cls-6,
#pika .cls-7 {
  stroke: #000222;
}

#pika .cls-11,
#pika .cls-3 {
  fill: #e19720;
}

#pika .cls-4 {
  fill: #b2b6ba;
}

#pika .cls-5 {
  opacity: 0.2;
}

#pika .cls-22,
#pika .cls-6 {
  fill: #bc181f;
}

#pika .cls-12,
#pika .cls-24,
#pika .cls-8 {
  fill: none;
}

#pika .cls-10,
#pika .cls-22,
#pika .cls-8 {
  stroke-width: 0.5px;
}

#pika .cls-10,
#pika .cls-23,
#pika .cls-9 {
  opacity: 0.1;
}

#pika .cls-13 {
  fill: #a85261;
}

#pika .cls-14 {
  fill: #db6b73;
}

#pika .cls-15,
#pika .cls-16 {
  fill: #aaaaba;
}

#pika .cls-17 {
  fill: #fff;
}

#pika .cls-18 {
  fill: #23235e;
}

#pika .cls-19 {
  fill: #9292a0;
}

#pika .cls-20 {
  fill: #f7f7f7;
}

#pika .cls-21 {
  fill: #7f754a;
}

a,
a:hover,
a:active,
a:focus,
a:visited {
  text-decoration: none;
  color: #000;
  font-weight: 600;
}

a:hover {
  color: #444;
}

nav, footer {
  display: flex;
  justify-content: flex-end;
  padding: 0 14px 0 0;
}

nav li, footer li {
  padding: 25px 10px;
  width: 25px;
  display: inline-block;
}

nav svg, footer svg {
  fill: #000;
}

nav svg:hover, footer svg:hover {
  fill: #444;
}

nav .email-link {
  align-self: center;
  margin-right: auto;
  background: #000;
  color: #FCF9A8;
  padding: 31px 20px;
}

nav .email-link:hover {
  background: #444;
}

hr {
  margin: 75px auto 0;
  max-width: 1000px;
  height: 3px;
  background-color: #f7f7f7;
  border: none;
}

@media (max-width: 1300px) {
  hr {
    margin-top: 0;
  }
}

footer {
  justify-content: center;
}

#welcome, nav, body {
  background: #FCF9A8;
}

#welcome {
  position: relative;
  height: calc(100vh - 73px);
}

#welcome p {
  font-size: 32px;
  width: 350px;
  font-weight: bolder;
  line-height: 38px;
  position: absolute;
  bottom: 400px;
  right: calc(50vw - 400px);
}

#welcome h1 {
  font-family: 'Montserrat', sans-serif;
  font-size: 288px;
  font-weight: bolder;
  text-align: center;
  position: absolute;
  bottom: 80px;
  text-align: center;
  width: 100%;
}

#welcome #cta {
  position: absolute;
  bottom: 50px;
  width: 100%;
  text-align: center;
  font-size: 22px;
  font-weight: 900;
}

#welcome img {
  position: absolute;
  left: calc(50vw - 429px);
  width: 500px;
  bottom: 22px;
}

@media (max-width: 868px) {
  #welcome h1 {
    font-size: 124px;
  }
  #welcome p {
    width: 240px;
    right: calc(50vw - 190px);
    bottom: 250px;
    font-size: 25px;
  }
  #welcome img {
    left: calc(50vw - 310px);
    bottom: 50px;
    width: 400px;
  }
}

#about {
  padding-top: 75px;
}

@media (max-width: 1300px) {
  #about {
    padding-top: 0;
  }
}

#about,
#music,
#tech {
  padding-left: 50px;
  padding-right: 50px;
  background: transparent;
  height: 800px;
  display: flex;
  justify-content: center;
  overflow: hidden;
  transition: all 1s ease-out;
}

#about.ready .graphic,
#about.ready .text,
#music.ready .graphic,
#music.ready .text,
#tech.ready .graphic,
#tech.ready .text {
  opacity: 0;
  transform: translateY(150px);
}

#about .graphic,
#about .text,
#music .graphic,
#music .text,
#tech .graphic,
#tech .text {
  transition: transform 1s ease-out, opacity 1s ease-out;
}

#about .graphic,
#music .graphic,
#tech .graphic {
  display: -ms-flexbox;
  display: flex;
  position: relative;
}

#about svg,
#music svg,
#tech svg {
  fill: #FCF9A8;
  width: 600px;
  margin: auto;
  margin-right: 0;
}

#about img,
#music img,
#tech img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 500px;
}

#about .text,
#music .text,
#tech .text {
  margin: auto 0 auto 150px;
  width: 450px;
  max-width: 550px;
}

#about .text h2,
#music .text h2,
#tech .text h2 {
  font-size: 2rem;
  font-weight: bold;
}

#about .text p,
#music .text p,
#tech .text p {
  line-height: 25px;
  margin-top: 18px;
}

#about .text a,
#music .text a,
#tech .text a {
  display: block;
  margin-top: 18px;
  font-weight: 700;
  cursor: pointer;
}

@media (max-width: 1300px) {
  #about,
  #music,
  #tech {
    height: 530px;
  }
  #about .text,
  #music .text,
  #tech .text {
    margin: auto 0 auto 95px;
  }
  #about svg,
  #music svg,
  #tech svg {
    width: 350px;
  }
  #about img,
  #music img,
  #tech img {
    width: 300px;
  }
}

@media (max-width: 868px) {
  #about,
  #music,
  #tech {
    flex-direction: column;
    align-items: center;
    justify-content: initial;
    height: auto;
    padding: 125px 0 75px 0;
  }
  #about .graphic,
  #music .graphic,
  #tech .graphic {
    height: 330px;
  }
  #about svg,
  #music svg,
  #tech svg {
    margin: auto;
    width: 330px;
  }
  #about .text,
  #music .text,
  #tech .text {
    margin: 100px 0 0 0;
    width: 85%;
  }
}

#tech .text {
  margin: auto 150px auto 0;
}

#tech .graphic img {
  left: 58%;
}

#tech #codepen-logo {
  fill: #fff;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 400px;
}

@media (max-width: 1300px) {
  #tech .text {
    margin: auto 95px auto 0;
  }
  #tech #codepen-logo {
    width: 200px;
  }
}

@media (max-width: 868px) {
  #tech {
    padding-top: 50px;
    padding-bottom: 75px;
    flex-direction: column-reverse;
  }
  #tech .text {
    margin: 100px 0 0 0;
  }
}

#music .graphic img {
  left: 58%;
  width: 700px;
}

@media (max-width: 1300px) {
  #music .graphic img {
    width: 400px;
  }
}

@media (max-width: 868px) {
  #music {
    padding-top: 50px;
    padding-bottom: 75px;
  }
}
